.link {
  color: black;
  display: block;
  text-decoration: none; /* remove underlines from links */
  border-style: solid;
  border-width: 1px;
  border-color: #E8E8EE;
  border-radius: 0.7em;
  box-shadow: 1px 1px 2px #E8E8EE;
  margin: 1.4em 0;
  padding: 0.3em 0 0.7em 1em;
}

.link:hover{
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1);
}


.wrapper {
  width: 900px;
  max-width: 90%;
  margin: 0 auto;
}

.main-header {
  margin: 1em 0 2em 0;
}

.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}

.clearfix:after {
  clear: both;
}

.logo img {
  width: 40px;
  float: left;
  margin-top: 1.9em;
  border-radius: 100%;
}

.logo h1 {
  float: left;
  margin: 0 0 0 0.7em;
  font-size: 1.5em;
  padding-top: 1.6em;
  color: #282828;
}

.nav {
  font-weight: bold;
  font-size: 16px;
  float: right;
  padding-top: 2.7em;
}

.nav a {
  color: #696969;
  margin-left: 1.5em;
  text-decoration: none;
}
.sort {
  text-align: right;
}

select {
  margin-left: 7px;
}

.company-logo {
  float: left;
}

.company-logo img{
  width: 115px;
  margin-right: 2em;
  margin-left: 0.3em;
  border-radius: 0.25em;
}

.job-title {
  font-size: 19px;
  font-weight: bold;
  color: #8c1515;
}

.job-details p{
  font-size: 15px;
  margin: 10px;
  color: #484848;
}

.icon {
  padding-right: 6px;
  width: 14px;
}

.job-type {
  text-transform: uppercase;
  float: right;
  white-space: nowrap;
  font-weight: 700;
  padding-right: 25px;
  position: relative;
}

.full-time {
  color: #5ec1e6;
}

.part-time {
  color: rgb(255, 143, 159);
}

.internship {
  color: #ffc53c;
}

.job-description {
  margin-top: -0.5em;
}

hr {
  margin: 2em 0;
  height: 1px;
  border: none;
  background-color: #dadada;
}